import React, {SyntheticEvent} from "react";
import styles from "../Counter/Counter.module.css";
import {appConstants} from "../../headers/appConstants";
import {NavLink, Redirect} from "react-router-dom";
import {putData} from "../../actions/SqlAction";
import Data from "../home/Data3";

const Logout = () =>{

        // let newUserData = {
        //     username: 'Login',
        //     password: 'password',
        //     id:2,
        // };
        //
        // putData('users',newUserData);

    const submitHander = (event:SyntheticEvent) => {
        event.preventDefault();

        console.log('submit???')
    };




        let CurrentCartList = new Array(Data.length+1).fill(0);
        for (let i = 0; i < Data.length; i++) {
            let qty = localStorage.getItem('product'+i);
            if(qty != null)
                CurrentCartList[i] = qty;
        }

        // save car data to DB, for next time login
        putData('usercart',{
            username: localStorage.getItem('CurrentUser'),
            cart_data: CurrentCartList.toString()
        })


        localStorage.clear();

    return(
        <>
            <Redirect to="/Home"/>
            <form className="login-form" onSubmit={submitHander}>
                <button className={styles.button}>
                    <NavLink className="navbar-brand" to={appConstants.homePageRoute}>
                        back to Home
                    </NavLink>
                </button>
            </form>

        </>
    );
}

export default Logout;