import React, {createRef, useCallback, useEffect, useRef, useState} from "react";
import {Counter} from "../../Counter/Counter";
import styles from "../../Counter/Counter.module.css";
import Data from "../Data3";
import css from "../Page1.module.scss";
import {useSelector} from "react-redux";
import {selectCount} from "../../Counter/counterSlice";
import {Link, NavLink, Redirect, useHistory} from "react-router-dom";
import {appConstants} from "../../../headers/appConstants";
import PetsIcon from "@mui/icons-material/Pets";
import {getAveStar, printStarInDetail} from "../details/Review";
import { postData} from "../../../actions/SqlAction";
import CounterV2 from "../../Counter/CounterV2";
import CounterV3 from "../../Counter/CounterV3";


const Cart = () => {
    const history = useHistory();

    const [,updateState] = useState({});
    const forceUpdateCart = useCallback(()=>{updateState({})},[]);


    let [processTOCartMSG,setProcessTOCartMSG] = useState('');

    // let item = Data[0];
    let item = Data[+window.location.pathname[6]-1];
    const count = useSelector(selectCount);


    let [productsLst,setProductsLst] = useState([]);
    function getProductsQty(){
        for (let i = 0; i < Data.length+1; i++) {
            let qty = +localStorage.getItem('product'+i);
            if(+localStorage.getItem('product'+i) == null)
                CurrentProductsQty.push(0);
            else
                CurrentProductsQty.push(qty);
        }
        setProductsQty(CurrentProductsQty);
    }


    useEffect(()=>{
        let newProductsLst = [];
        for (let i = 0; i < 10; i++) {
            if(localStorage.getItem('product'+i) != null)
                newProductsLst.push(Data[i-1]);
        }
        setProductsLst(newProductsLst);
        getProductsQty();
        // console.log('productsLst',productsLst) // productsLst []
    },[]);
    // },[productsLst]);


let emptyCart =  <>

    <NavLink className={css.LinkTagBlackColor} to={appConstants.homePageRoute}>Home </NavLink>
    &gt; <NavLink className={css.LinkTagBlackColor} to={appConstants.shopRoute}> Products </NavLink>
    &gt; View Cart
    <h1 className='text-center'>YOUR CART </h1>
    <h5>Product</h5>

    <div className={css.cart_wrapper}>
        <div className={css.wrapper}>
            {/*========= 111111111 =============*/}
            <div>
                <img className="card-img-top text-center" src={'https://static.vecteezy.com/system/resources/previews/005/006/007/original/no-item-in-the-shopping-cart-click-to-go-shopping-now-concept-illustration-flat-design-eps10-modern-graphic-element-for-landing-page-empty-state-ui-infographic-icon-vector.jpg'}
                     alt={'empty cart'}
                />
            </div>
            <div> <h1> Oops! Your Cart is empty</h1> </div>
        </div>
        {/*========== Summary =========*/}
        <div>
            <p><b>ORDER SUMMERY</b></p>
            {/*<h1>item: {item}</h1>*/}
            <h5><b>Subtotal:</b> $ 0 </h5>
            <Link to={`${appConstants.shopRoute}`}>
                <button className={styles.CHECKOUT}> Continue Shopping </button>
            </Link>
        </div>
    </div>
</>;


// let passingOrderStack = ()=>{
//     postData('samples',{
//         name:'mod-divi',
//         age: count * 10 + item.id
//     })
// }

    function CartCount(props) {
        const handleCartCountClick = () => {
            const newCount = props.cartCount + 1;
            props.onCartCountChange(newCount);
        };

        return (
            <div>
                {/*<div>{props.item.name}</div>*/}
                {/*<div>{props.item.price}</div>*/}
                <div>
                    <button className={styles.button} onClick={handleCartCountClick}>
                        {/*<AddShoppingCartIcon/>*/}
                        {/*Add to Cart ({props.cartCount})*/}
                        {props.cartCount}
                    </button>
                </div>
            </div>
        );
    }

    const [currentStock, setCurrentStock] = useState<number | null>(null);
    const updateCurrentStock = (newStock: number) => {
        setCurrentStock(newStock);
    };

    let sum = ()=>{
        let res = 0.0;
        for (let i = 0; i < Data.length; i++) {
            let price = +localStorage.getItem('productPrice'+i);
            if(price != null)
                res += price;
        }
        return res.toFixed(2);
    }


    const [productsQty, setProductsQty] = useState(new Array(Data.length+1).fill(0));
    let CurrentProductsQty: number[]=[];


    function removeItem(id){
        localStorage.removeItem('product'+id);
        // eslint-disable-next-line no-restricted-globals
        location.reload();
    }

    if(productsLst.length == 0 ) {
        return (emptyCart);
    }


    function lockStock(){
        // console.log('lst: ',productsLst) //0: {id: 5, name: 'Freeze-Dried Turkey Dinner Patties Dog Food', brand: 'VITAL ESSENTIALS', category: 'Dog', productsType: 'Food', …}
        // console.log('qty: ',productsQty) //(9)[0, 0, 0, 0, 0, 5, 0, 0, 0]
        // console.log('currentStock: ',currentStock)

        for (let i = 0; i < Data.length+1; i++) {
            if(productsQty[i]!=0){
                let CurStock = localStorage.getItem('Stock'+(i).toString());
                // console.log('===', (localStorage.getItem('Stock'+(i+1))));
                // console.log('i: ',i);
                // console.log('CurStock: ',CurStock);
                // console.log('qty: ',productsQty[i]);
                // console.log('qty productsQty: ',productsQty);

                localStorage.setItem('Stock'+(i), String((+CurStock) - (+productsQty[i])));
                // console.log('currentStock should be: ',)
                if((+CurStock) - (+productsQty[i]) < 0 && +(localStorage.getItem('product'+(i).toString()))!==0 ){
                    // console.log('product',i,localStorage.getItem('product'+(i)));
                    // console.log('productsQty[i]',productsQty[i]);
                    setProcessTOCartMSG('Warning:  Product'+i+ ' -> Out Of Stack! updates your cart!')
                    forceUpdateCart();
                    return null;
                }
            }
        }

        {/* orig use <Link to={`${appConstants.checkoutRoute}`} >*/}
        history.push(`${appConstants.checkoutRoute}`);
    }


    return(
        <>

{/*<div className={`styles.cart ${isCartOpen ? 'cart--open' : ''}`}>*/}
        <div >
            <NavLink className={css.LinkTagBlackColor} to={appConstants.homePageRoute}>Home </NavLink>
            &gt; <NavLink className={css.LinkTagBlackColor} to={appConstants.shopRoute}> Products </NavLink>
            &gt; View Cart
            <h1 className='text-center'>YOUR CART </h1>
            <h5 style={{padding:'10px'}}>Product</h5>

        <div className={css.wrapper_Products_Summery}>
            <div >

                {productsLst?.map((item)=>{
                    const stock = localStorage.getItem(`Stock${item.id}`) || '0';
                    // const stock = localStorage.getItem(`Stock${item.id}`) || '0';
                    // const newStock = parseInt(stock, 10) - (currentStock || 0);
                    let CurrentCount = localStorage.getItem('product'+item.id);


                    return(
                        <div className={css.wrapper}  key = {item.id}>
                            {/*========= 111111111 =============*/}
                            <div  style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                                <img className="card-img-top text-center" style={{ width: '70%', height: 'auto' , }} src={item?.img} alt={item.productsType}/>
                            </div>
                            {/*========== 222222222 ==========*/}
                            <div className='text-center'>
                                <h5><b>{item?.name}</b></h5>
                                <p><b>{item?.category} Food</b></p>
                                <p>${item?.price}</p>
                                {/*<p>{item?.descri}</p>*/}
                                <p><b>Package Size:</b> {item?.weight}</p>
                                {/*<p>Stock: <b>{stock-CurrentCount >=0? stock-CurrentCount: 'Out Of Stack'}</b> </p>*/}
                                <p>Stock: <b>{item.stock-CurrentCount >=0? item.stock-CurrentCount: 'Out Of Stack'}</b> </p>

                                <div style={{display:'inline-flex'}}>
                                    <CounterV2  initialCount = {+CurrentCount}
                                                updateCurrentStock={updateCurrentStock}
                                                productID={item.id}
                                    />
                                    &nbsp; &nbsp;
                                    <button className={styles.button} onClick={()=>{removeItem(item.id)}}> X </button>
                                    {/*<button className={styles.button} onClick={removeItem(item.id)}> X </button>*/}

                                </div> <br/>
                                <br/>

                                {/*<Counter/>*/}
                                <div></div>
                            </div>
                        </div>

                    );
                })

                }
                
            </div>



            <div>
                {/*========== Summary =========*/}
                <div>
                    <p><b>ORDER SUMMERY</b></p>
                    <h5><b>Items:</b> </h5>
                    {productsLst?.map((item)=>{
                        // let stock = localStorage.getItem(`Stock${item.id}`) || '0';
                        // let ItemInCheckoutCount = +localStorage.getItem('product'+item.id) > +stock ? 0 : localStorage.getItem('product'+item.id);
                        let ItemInCheckoutCount = +localStorage.getItem('product'+item.id) > + item.stock ? 0 : localStorage.getItem('product'+item.id);

                        localStorage.setItem('productPrice'+item.id,(ItemInCheckoutCount*item?.price).toFixed(2));
                        return(
                               <div key = {item.id}>
                                   <h5>&nbsp;&nbsp;&nbsp;&nbsp; productID[<b>{item?.id}</b>] x {ItemInCheckoutCount}  -- ${(ItemInCheckoutCount*item?.price).toFixed(2)}</h5>
                               </div>
                       );
                    })}


                    <h5><b>Subtotal:</b> ${sum()} </h5>

                    {/*replaced this <Link> with useHistory()*/}
                    {/*<Link to={`${appConstants.checkoutRoute}`} >*/}
                        <button className={styles.CHECKOUT} onClick={lockStock}>
                            PROCEED TO CHECKOUT
                        </button>
                    {/*</Link>*/}

                    <p style={{color:'red'}}> <br/> {processTOCartMSG}</p>

                </div>
            </div>
        </div>
    </div>
</>

    );
}

export default Cart;