export default Checkout;


function CountdownTimer() {
    const [count, setCount] = useState(900);

    useEffect(() => {
        if (count <= 0) {
            return;
        }

        const timer = setTimeout(() => {
            setCount(count - 1);
        }, 1000);

        return () => clearTimeout(timer);
    }, [count]);

    return (
        <div>
           <h5> <b> {count === 0 ? 'Time is up! Release/unlock Cart!' : `After: ${count} sec`}</b> </h5>
        </div>
    );
}