import React, { useState} from "react";
import styles from "./Counter.module.css";


interface CounterProps {
    initialCount?: number;
    updateCurrentStock: (newStock: number) => void;
}

function CounterV2({ initialCount = 0, updateCurrentStock,productID }: CounterProps) {
    const [count, setCount] = useState<number>(initialCount || 0);

    const handleIncrement = () => {
        setCount(count + 1);
        updateCurrentStock(count + 1);
        localStorage.setItem('product'+productID,String(count + 1));
    };

    const handleDecrement = () => {
        if (count > 0) {
            setCount(count - 1);
            updateCurrentStock(count - 1)
            localStorage.setItem('product'+productID,String(count - 1));
        }
    };

    const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const value = parseInt(event.target.value, 10);
        if (!isNaN(value)) {
            updateCurrentStock(value - count);
            setCount(value);
            localStorage.setItem('product'+productID,String(count));
        }
    };





    return (
        <div>
            <button className={styles.button} onClick={handleIncrement}> + </button>
            &nbsp;
            <input  className={styles.textbox}   value={count} onChange={handleInputChange} />
            <button className={styles.button} onClick={handleDecrement}> - </button>
        </div>
    );
}

export default CounterV2;
