import React, {useCallback, useEffect, useState} from "react";
import {Link, NavLink} from "react-router-dom";
import css from "../Page1.module.scss";
import Data from "../Data3";
import {Counter} from "../../Counter/Counter";
import styles from "../../Counter/Counter.module.css";
import {appConstants} from "../../../headers/appConstants";
import Review, {getAveStar, printStarInDetail} from "./Review";
import {useSelector} from "react-redux";
import {selectCount} from "../../Counter/counterSlice";
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";
import {getData} from "../../../actions/SqlAction";
// import {Formik} from "formik";

// class Detail extends Component<DetailProps, DetailState>{
// class Detail extends Component<any, any>{
const Detail =()=>{
    // render() {
    //     let [itemDetail, setItemDetail] = useState([]);


        const itemID = +window.location.pathname.substring(8);

        // const item = Data[+window.location.pathname[8]-1];
        // let item = itemDetail;
        let item2: {
            name: string,
            id: number,
            stock: number,
            price: number,
            category: string,
            weight: string,
            descri: string,
            img: string,
        }[] = [];

        const [,updateState] = useState({});
        const forceUpdate = useCallback(()=>{updateState({})},[]);


    const count = useSelector(selectCount);

    let [item,setitem] = useState<{ name: string; id: number; stock: number; price: number; category: string; weight: string; descri: string; img: string; }[]> ([]);
    let [stock,setStock] = useState(0);

    // let [item,setitem] = useState();


    let stockRemain = stock - count;

    useEffect(()=>{

            // setStock(+localStorage.getItem('Stock'+itemID));

            getData('productdata')
                .then((products)=>{
                    for (let i = 0; i < products.length; i++) {
                        if(products[i].id === +window.location.pathname.substring(8) ) {
                            // setItemDetail(products[i]);
                            setitem(products[i])

                            setStock(products[i].stock)

                            // item2 = products[i];
                            // setitem(item2)
                            // console.log("changed item -> ",item2)
                        }
                    }
                })

    },[])

    const [cartCounts1, setCartCounts1] = useState(new Array(10).fill(0));
    const handleCartCountChange = (index, newCount) => {
        const newCartCounts = [...cartCounts1];
        newCartCounts[index] = newCount;
        setCartCounts1(newCartCounts);
        localStorage.setItem('product'+index.toString(),newCount);
    };
    function CartCount(props) {
        const handleCartCountClick = () => {
            const newCount = props.cartCount + 1;
            props.onCartCountChange(newCount);
        };

        return (
            <div>
                <button className={styles.button} onClick={handleCartCountClick}>
                    AddToCart
                    {' '}
                    <AddShoppingCartIcon/>
                    {' '}
                    {/*{props.cartCount}*/}
                </button>
            </div>
        );
    }


    return (
        <body>

        <p>
            <NavLink
                className={css.LinkTagBlackColor}
                to={appConstants.homePageRoute}>Home </NavLink>
            &gt;
            <NavLink className={css.LinkTagBlackColor} to={appConstants.shopRoute}> Products </NavLink>
            &gt; Detailas
        </p>

        <div className={css.wrapper} >
            {/*========= 111111111 =============*/}
            <div><img className="card-img-top text-center" src={item.img} alt={item.productsType} onClick={forceUpdate}/></div>

            {/*========== 222222222 ==========*/}
            <div className='text-center'>
                <h5><b>{item?.name}</b></h5>
                <p><b>{item.category} Food</b></p>
                <p>Reviews: {(getAveStar)()} {printStarInDetail(Number((getAveStar)())-1)}</p>
                <p>${item.price}</p>
                <p>{item.descri}</p>
                <p>Package Size: <b>{item.weight}</b> </p>
                <p>Stock: <b>{stockRemain >0? stockRemain : 'Out Of Stack'}</b></p>

                <Counter/>

                {/*<button onClick={SaveItemToCart}> AddToCart </button>*/}


                <div style={{'textAlign':'center', 'display': 'inline-flex'}}>
                    <CartCount
                        cartCount={cartCounts1[item.id]}
                        onCartCountChange={(newCount) => handleCartCountChange(item.id, newCount)}
                    />

                    &nbsp;&nbsp;&nbsp;&nbsp;
                    <Link to={`${appConstants.cartRoute}/${item.id}`}>
                        {/*<button className={styles.AddToCart} onClick={SaveItemToCart}>*/}
                        <button className={styles.AddToCart}>
                            Go To the Cart
                        </button>
                    </Link>


                </div>

                <p></p>
                <p>


                </p>
            </div>

            {/*=========== 333333333 ===========*/}
            <div style={{padding:'30px'}}>
               <h2><b>Description</b></h2>
                    <h4>Product Description:</h4>
                    <p><b>Curr ID: {itemID}</b></p>
                    <p><b>{item.descri}</b></p>

                    {/*<p>Real chicken for purrfect nutrition! This complete and balanced healthy cat food has everything your little hunter needs to thrive and play with true feline confidence.</p>*/}

                    {/*<p>Chicken is a lean and low-sodium protein source, making it a healthy option to fuel your cat’s vitality. This freeze-dried chicken cat food is made with real meat, vital organs and bone. Our proprietary “slow” freeze-drying process does not destroy natural enzymes and nutrients in the food.</p>*/}

                    {/*<p>Combat food allergies by excluding “junky” ingredients like additives and artificial preservatives. Our limited ingredient, grain free chicken cat food utilizes real, raw protein instead of unhealthy, carb-heavy fillers. This natural cat food can be fed as a fully balanced meal, or as a healthy protein supplement to take your cat’s diet to the next level.</p>*/}
                    <p>Raw food, like Vital Essentials®, is grain free and gluten free, which reduces the risk of obesity and allergies. If your cat has packed on a few pounds or is showing signs of allergies, look no further than Vital Cat. Our gluten free, grain free raw cat food is high in protein to meet the needs of an obligate carnivore (that’s your kitty).</p>
            </div>
            {/*========== 444444444 ===========*/}
            <div className={css.reviews} style={{padding:'3%'}}>
                <h1>Customer Reviews</h1>
                <p><b>Makes breakfast taste like dessert!</b></p>
                <p>
                    My puppy usually ignores breakfast, until I started adding Vital Essentials Beef Topper to his food. He starts jumping out of excitement and gobbles it all up! This topper is a great way to add in some additional protein and get your pets excited to eat!
                </p>

                <img src={'https://marvel-b1-cdn.bc0a.com/f00000000236716/dcdn.aitrillion.com/ai-review/jxjhP8Kiqelhc2y0-GbOAOr5cOV148hE81LluK9MJTQ/1651242543vitalessentials_2.jpg'}
                    width={'400px'}
                    alt={'Dog Treats'}
                />
            </div>
        </div>

        <div className={css.inner_grid_filter} >
            <Review/>
        </div>
        </body>
        );
    // }
}

export default Detail;

