import React, {SyntheticEvent, useCallback, useEffect, useState} from "react";
import {appConstants} from "../../../headers/appConstants";
import {Link} from "react-router-dom";
import styles from "../../Counter/Counter.module.css";
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import {getData} from "../../../actions/SqlAction";


const CardPet = ({item}:any) => {

    // let getAll = getData('addtocart');
    //
    // const  blackTest1 ={
    //         'color': "red",
    //         'text-decoration': "none"
    // }

    const [,updateState] = useState({});
    const forceUpdate = useCallback(()=>{updateState({})},[]);
    let [stock,setStock] = useState(0);


    let CurrentItemInCartLst1: number[]=[];

    // const [cartCounts1, setCartCounts1] = useState(new Array(item.length+1).fill(0));
    const [cartCounts1, setCartCounts1] = useState(new Array(item.length+1).fill(0));

    const [productLst,setProductLst] = useState([]);


    useEffect(()=>{

        getData('productdata')
            .then((p)=>{
                let list = [];
                for (let i = 0; i < p.length; i++) {
                    localStorage.setItem('Stock'+p[i].id,p[i].stock);
                    list.push(p[i]);
                }
                setProductLst(list);
            })

        for (let i = 0; i < 10; i++) {
            let temp = +localStorage.getItem('product'+i);
            if(+localStorage.getItem('product'+i) == null){
                CurrentItemInCartLst1.push(0);
            }else{
                CurrentItemInCartLst1.push(temp);
            }
        }
        setCartCounts1(CurrentItemInCartLst1);

        // getData('samples')
        //     .then((res)=>{
        //         for (let i = 0; i < res.length; i++) {
        //             if(+res[i].name === +item.id)
        //                 setStock(res[i].age);
        //
        //             if(parseInt(res[i].name,10) > 0 && parseInt(res[i].name,10) < 10){
        //                 localStorage.setItem('Stock'+res[i].name, res[i].age);
        //             }
        //         }
        //     })

    },[])

    const handleCartCountChange = (index, newCount) => {
        const newCartCounts = [...cartCounts1];
        newCartCounts[index] = newCount?newCount:0;
        console.log('newCartCounts[ item:',index,']: ',newCartCounts[index],)
        setCartCounts1(newCartCounts);
        localStorage.setItem('product'+index.toString(),newCount);

    };

    function CartCount(props) {
        const handleCartCountClick = () => {
            const newCount = props.cartCount + 1;
            props.onCartCountChange(newCount);
        };

        return (
            <div>
                {/*<div>{props.item.name}</div>*/}
                {/*<div>{props.item.price}</div>*/}
                <div>
                    <button className={styles.button} onClick={handleCartCountClick}>
                        <AddShoppingCartIcon/>
                        {/*Add to Cart ({props.cartCount})*/}
                       {props.cartCount}
                    </button>
                </div>
            </div>
        );
    }

    return (
        <>
            <div className="container-fluid">
                <div className="row justify-content-center">

                    {productLst.map((val:any) => {
                        return (
                            <div
                                className="col-md-4 col-sm-6 card my-3 py-3 border-0"
                                key={val.id}
                            >
                                <div className="card-img-top text-center">
                                    <img src={val.img} alt={val.name} className="photo w-75" />
                                </div>

                                <div className="card-body">
                                    <div className="card-title fw-bold fs-5">
                                        <Link
                                            className='blackTest'
                                            to={`${appConstants.detailRoute}/${val.id}`}
                                        >
                                            <td>{val.name}</td>
                                        </Link>

                                        <br/>
                                        $ {val.price}

                                        <div style={{'textAlign':'right'}}>
                                            <CartCount
                                                cartCount={cartCounts1[val.id]}
                                                onCartCountChange={(newCount) => handleCartCountChange(val.id, newCount)}
                                            />
                                        </div>
                                    </div>
                                    <div>Package Size: {val.weight}</div>
                                    <div className="card-text">{val.descri}</div>
                                </div>
                            </div>
                        );
                    })}
                </div>
            </div>
            <style>
                {`.blackTest{
                    color: black;
                    text-decoration: none; 
                }`}
            </style>
        </>
    );
};

export default CardPet;
