import React, {SyntheticEvent, useCallback, useEffect, useState} from "react";
import InsertCommentIcon from "@mui/icons-material/InsertComment";
import StarHalfIcon from "@mui/icons-material/StarHalf";
import PetsIcon from "@mui/icons-material/Pets";
import styles from "../../Counter/Counter.module.css";
import css from "../Page1.module.scss";
import {getData, putData} from "../../../actions/SqlAction";
import {appConstants} from "../../../headers/appConstants";
import {useHistory} from "react-router-dom";

let allStar = 0;
let lenOfComLst =1;
function setAllStar(newAveStar:number){
    allStar = newAveStar;
}
function setLenOfComLst(newLen:number){
    lenOfComLst = newLen;
}

export function getAveStar(){
    return (allStar/lenOfComLst).toFixed(2);
}

export let printStarInDetail = (x:number)=>{
    if(x >= 5) return '★★★★★';
    else if(x < 1) return '☆';

    let lst ='★';
    while(x-- > 1)
        lst += '★';
    return lst;
};


const Review = ()=>{
    const history = useHistory();
    const [currentCommentsListLength,setCurrentCommentsListLength] = useState(100);
    // const [totalStar,setTotalStar] = useState(0);

    const [commentLst, setCommentLst] = useState<{ id: any; name: any; review: any; stars: any; reviewdate: any; }[]>();
    let lstArray: { id: any; name: any; review: any; stars: any; reviewdate: any; }[] = [] ;

    let totalStars = 0.0;

    let [commentsPage,setCommentsPage] = useState(1);
    let [newCommentLst,setNewCommentLst] = useState<{ id: any; name: any; review: any; stars: any; reviewdate: any; }[]>();
    let currentPageComments: { id: any; name: any; review: any; stars: any; reviewdate: any; }[] = [] ;
    let changeCommentsPage =(CommentLst, currPage)=>{
        currentPageComments =[];
        for (let i = (currPage-1)*4; i < CommentLst.length && i< currPage*4; i++) {
            // console.log('CommentLst[',i,']: ',CommentLst[i])
            if(CommentLst[i]){
            currentPageComments.push(CommentLst[i])
            }
        }
        setNewCommentLst(currentPageComments);
    }


    useEffect(
        ()=>{
            getData('comments')
            // geta()
                .then(
                    (result)=>{
                        for (let i =0; i< result.length; i++){
                            let a = {
                                    id: result[i].id,
                                    name: result[i].name,
                                    review: result[i].review,
                                    stars: result[i].stars,
                                    reviewdate: result[i].reviewdate,
                                }
                            totalStars += result[i].stars===666?5:result[i].stars;
                            lstArray.push(a);

                            // if(result.length>4 && i === 3){
                            //     let x = lstArray.length + '';
                            //     seterrorMSG(x);
                            //     console.log("NewCommentLst: ",newCommentLst)
                            //     setNewCommentLst(lstArray);
                            //     console.log("NewCommentLst: ",lstArray)
                            //
                            // }else if(result.length<4){
                            //     setNewCommentLst(lstArray);
                            // }

                        }

                            changeCommentsPage(lstArray,1);
                            setCommentLst(lstArray);

                            setCurrentCommentsListLength(result.length+1);

                        // console.log("Review-> Effec -> totalsatr: ",totalStars);
                        setAllStar(totalStars);
                        setLenOfComLst(result.length);

                    })
                .catch(
                    (failed_result)=> console.log('failed_result: ',failed_result))



        }, []
    )

    const [comment, setComment] = useState({
        id: null,
        name: '',
        review: '',
        stars: 5,
        reviewdate: '',
    })

    const [,updateState] = useState({});
    const forceUpdateReview = useCallback(()=>{updateState({})},[]);

    const [commentMSG,setCommentMSG] = useState('');
    const submitHander =(event:SyntheticEvent)=>{
        event.preventDefault();
        forceUpdateReview();


        const date = new Date();
        let  exactDate = new Date();
        // let  exactDate = new Date().toJSON();

        let day = date.getDate();
        let month = date.getMonth() + 1;
        let year = date.getFullYear();
        let currentDate = `${month}-${day}-${year}`;

        console.log('currentDate: ',currentDate);
        console.log('exactDate: ',exactDate);
        setCurrentCommentsListLength(currentCommentsListLength+1);

        let newCommentsData = {
            // id: (commentLst?.length||100) +20,
            id: currentCommentsListLength+1,
            name: comment.name,
            review: comment.review,
            stars: comment.stars===666?666:comment.stars>5?5:comment.stars<0?0:comment.stars,
            reviewdate: currentDate,
            // reviewdate: exactDate,
        };


        // get current username
        getData('users/getAll')
            .then((CurrentUserName)=>{
                let n = CurrentUserName[1].username;
                n = n.substring(0,n.length-1);
                let cur_user = n;

                // check if the user quality for comment or not ( buy product or not)
                if(cur_user === 'admin2')
                    putData('comments',newCommentsData);
                else{
                    getData('addtocart')
                        .then((res)=>{
                            for (let i = 0; i < res.length; i++) {
                                if(res[i].username === cur_user){
                                    putData('comments',newCommentsData);
                                    return;
                                }
                            }
                            setCommentMSG('Please, order item first!');
                        })
                }
            });
        forceUpdateReview();
        // history.push(`${appConstants.checkoutRoute}`);
        // history.push(`/detail/2`);
    }

    const commentUpdateHandler = (event: SyntheticEvent) => {
        const key = (event.target as HTMLInputElement).name;
        const value = (event.target as HTMLInputElement).value;
        setComment({
            ...comment, // copy the old state
            [key]: value
        })
    };


    const printStar = (len:number)=>{
        let lst ='★';
        if(len === 666) return <span><PetsIcon/><PetsIcon/><PetsIcon/><PetsIcon/><PetsIcon/></span>;
        else if(len >= 5) return '★★★★★';
        else if(len < 0) return '☆';
        while(len-- > 1)
            lst += '★';
        return lst;
    }


let commentForm =
    <form onSubmit={submitHander}>

        <p style={{color:'red'}}>{commentMSG}</p>

       <InsertCommentIcon/>
       <input
           name="name"
           className='UserTextBox'
           value = {comment.name}
           onChange={commentUpdateHandler}
           placeholder={'title'}
       />

        <StarHalfIcon />
        <input
            name="stars"
            className='UserTextBox'
            value = {comment.stars}
            type = 'number'
            onChange={commentUpdateHandler}
            placeholder={'stars 1-5'}
        />
       <button className={styles.button}>
           Submit Comment!
       </button>

    <div>
        <PetsIcon/>
        <textarea
            name="review"
            className='UserTextBox'
            value = {comment.review}
            onChange={commentUpdateHandler}
            placeholder={'review in 400 words'}
        />
    </div>
</form>;


    const [LoginData, setLoginData] = useState("Guest!");

    // const CurrentUser = LoginData.substring(0,LoginData.length-1);

    getData('users/getAll')
        .then((usersList)=> {
            setLoginData(usersList[1].username);
        })
        .catch((failed_result)=> console.log('sth wrong: ',failed_result));

    return(
        <>
            <h2 style={{'textAlign':'center'}} onClick={()=>{
                forceUpdateReview();
                console.log('forceUpdateReview!!!');
            }
            }>Customer Reviews</h2>

            {(()=>{
                if(LoginData === 'Login'){
                    return <p style={{textAlign: 'center'}}>i dont who it is, but someone didnt login yet ;( </p>;
                }else{return (
                    // <p style={{textAlign:'center'}}>
                    <p>
                    {/*<p style={{textAlign: 'center'}}>*/}
                        {commentForm}
                    </p>
                        )}
            })()}

            {
                newCommentLst?.map(
                    (p: any)=> (
                        // <div style={{textAlign:'center'}}>
                        <div className={css.reviewsSmall}>
                            <p>
                                <b>Customer: </b> {p?.id}
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                <b>stars: </b>{printStar(p?.stars)}
                                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                                {p?.reviewdate}
                            </p>
                            <p><h5><b>{p?.name}</b></h5> </p>
                            <p> {p?.review} </p>
                            <br/><br/><br/>
                        </div>
                    ))
            }

            <div style={{textAlign:'center'}}>

                <button
                    className={styles.button}
                    aria-label="Decrement value"
                    onClick={() => {
                        if( commentsPage > 1){
                            setCommentsPage(commentsPage-1);
                            changeCommentsPage(commentLst,commentsPage-1);
                            forceUpdateReview();
                        }
                    }}
                >
                    {'<'}
                </button>

                &nbsp;  Page:<b> {commentsPage} </b>  &nbsp;
                {/*<span className={styles.value}>{count}</span>*/}
                {/*<span className={styles.value}> count not working</span>*/}

                <button
                    className={styles.button}
                    aria-label="Increment value"
                    onClick={()=>{
                        if( commentsPage < commentLst?.length/4 ){
                            setCommentsPage(commentsPage+1);
                            changeCommentsPage(commentLst, commentsPage+1);
                            forceUpdateReview();
                        }
                    }}>
                    {'>'}
                </button>
            </div>


        </>
    );
}

export default Review;

