import React, {useCallback, useEffect, useState} from 'react';
import {NavLink} from "react-router-dom";
import {appConstants} from "./appConstants";


const Header = (props:any) =>{


    const CurrentUser = localStorage.getItem('CurrentUser');

    const [,updateState] = useState({});
    const forceUpdateHeader = useCallback(()=>{updateState({})},[]);

    let LoginHeaderInfo;
    if(CurrentUser == null){
        LoginHeaderInfo =
           <div>
               <li className="nav-item">
                   <NavLink className="nav-link" to={appConstants.loginRoute} onClick={forceUpdateHeader}> Please, Login! </NavLink>
                   {/*<b className="nav-link">Please,</b>*/}
               </li>
           </div>
    }else if(CurrentUser === 'admin2'){
        LoginHeaderInfo =
                    <ul className="nav navbar-nav" >
                        <li className="nav-item">
                            <NavLink className="nav-link" to={appConstants.page2Route}> Test_GetData </NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to={appConstants.loginRoute}> Login </NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to={appConstants.checkoutRoute}> Checkout(hide) </NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to={appConstants.registerRoute}> Register(hide) </NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to={appConstants.reviewRoute}> Review(hide) </NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to={appConstants.db_sqlRoute}> History</NavLink>
                        </li>

                        <b><NavLink style={{'color':'grey', 'textDecoration':'none'}} className="link"  to={'/home'} onClick={forceUpdateHeader}> Switch!</NavLink></b>

                        <ul className="nav navbar-nav">
                            <li className="nav-item" >
                                <b className="nav-link">
                                    admin account!
                                    <b><NavLink style={{'color':'grey', 'textDecoration':'none'}} className="link" to={appConstants.logoutRoute} onClick={forceUpdateHeader}> Logout</NavLink></b>
                                </b>
                            </li>
                        </ul>
                    </ul>

    }else if(CurrentUser === 'admin'){
        LoginHeaderInfo =
            <ul className="nav navbar-nav" >

                <li className="nav-item">
                    <NavLink className="nav-link" to={appConstants.loginRoute}> Login </NavLink>
                </li>


                <li className="nav-item">
                    <NavLink className="nav-link" to={appConstants.registerRoute}> Register(hide) </NavLink>
                </li>

                <li className="nav-item">
                    <NavLink className="nav-link" to={appConstants.reviewRoute}> Review(hide) </NavLink>
                </li>

                <li className="nav-item">
                    <NavLink className="nav-link" to={appConstants.db_sqlRoute}> History</NavLink>
                </li>


                <ul className="nav navbar-nav">
                    <li className="nav-item" >
                        <b className="nav-link">
                            admin account!
                            <b><NavLink style={{'color':'grey', 'textDecoration':'none'}} className="link" to={appConstants.logoutRoute} onClick={forceUpdateHeader}> Logout</NavLink></b>
                        </b>
                    </li>
                </ul>
            </ul>

    }else{
        LoginHeaderInfo =
            <div  >
                <li className="nav-item">
                    <b className="nav-link">
                        Welcome, {CurrentUser} !
                        {/*Welcome, Guest!*/}
                      <b><NavLink style={{'color':'grey', 'textDecoration':'none'}} className="link" to={appConstants.logoutRoute} onClick={forceUpdateHeader}> Logout</NavLink></b>
                    </b>
                </li>

            </div>
    }



    return(
        <header>
            <div style={{  justifyContent: 'flex-start' }}>
            {/*    display: 'flex',*/}
            <nav className="navbar navbar-dark bg-dark navbar-expand-sm">
                <ul className="nav navbar-nav">
                <NavLink className="navbar-brand" to={appConstants.homePageRoute}> Pet GiuGiu </NavLink>
                    {/*<li className="nav-item">*/}
                    {/*    <NavLink className="nav-link" to={appConstants.namesRoute}> Names </NavLink>*/}
                    {/*</li>*/}

                    {/*<li className="nav-item">*/}
                    {/*    <NavLink className="nav-link" to={appConstants.ADD_NAME}> Add Name </NavLink>*/}
                    {/*</li>*/}

                    {/*<li className="nav-item">*/}
                    {/*    <NavLink className="nav-link" to={appConstants.productsRoute}> Product </NavLink>*/}
                    {/*</li>*/}

                    {/*<li className="nav-item">*/}
                    {/*    <NavLink className="nav-link" to={appConstants.addProductRoute}> Add Product</NavLink>*/}
                    {/*</li>*/}


                    {/*<li className="nav-item">*/}
                    {/*    <NavLink className="nav-link" to={appConstants.reactCounterRouter}> React </NavLink>*/}
                    {/*</li>*/}

                    {/*<li className="nav-item">*/}
                    {/*    <NavLink className="nav-link" to={appConstants.page2Route}> Test_GetData </NavLink>*/}
                    {/*</li>*/}

                    {/*=============================*/}
                    {/*=============================*/}
                    <li className="nav-item">
                        <NavLink className="nav-link" to={appConstants.homePageRoute} onClick={forceUpdateHeader}>
                            {/*&nbsp; &nbsp; &nbsp; &nbsp;  */}
                            Home </NavLink>
                    </li>

                    <li className="nav-item">
                        <NavLink className="nav-link" to={appConstants.shopRoute} onClick={forceUpdateHeader}> Shop </NavLink>
                    </li>


                    <li className="nav-item">
                        <NavLink className="nav-link" to={appConstants.cartRoute} onClick={forceUpdateHeader}> Cart </NavLink>
                    </li>

                    {/*<li className="nav-item">*/}
                    {/*    <NavLink className="nav-link" to={appConstants.loginRoute}> Login </NavLink>*/}
                    {/*</li>*/}

                    {/*<li className="nav-item">*/}
                    {/*    <NavLink className="nav-link" to={appConstants.detailRoute}> Detail(hide) </NavLink>*/}
                    {/*</li>*/}

                    {/*<li className="nav-item">*/}
                    {/*    <NavLink className="nav-link" to={appConstants.checkoutRoute}> Checkout(hide) </NavLink>*/}
                    {/*</li>*/}

                    {/*<li className="nav-item">*/}
                    {/*    <NavLink className="nav-link" to={appConstants.registerRoute}> Register(hide) </NavLink>*/}
                    {/*</li>*/}

                    {LoginHeaderInfo}

                    {/*<h1 style={{'color':'white'}}>*/}
                    {/*    Log:{LoginData}*/}
                    {/*    CurrentUser: {CurrentUser}*/}
                    {/*</h1>*/}

                </ul>
            </nav>
            </div>
        </header>

    );
}

export default Header;
